import React, { useState } from 'react';
import { 
  FileBarChart, Users, Clock, DollarSign, PieChart, Briefcase, 
  Printer, X, Wallet, Filter, Calendar, FileText, ArrowLeft, Download
} from 'lucide-react';
import { 
  MOCK_ATTENDANCE, 
  MOCK_PAYROLL, 
  MOCK_EMPLOYEES, 
  MOCK_CANDIDATES, 
  MOCK_LEAVE_BALANCES,
  MOCK_LOANS
} from '../constants';
import DataControls from '../components/DataControls';

interface ReportType {
  id: number;
  title: string;
  desc: string;
  icon: any;
  color: string;
  bg: string;
  dataKey: string;
}

const Reports: React.FC = () => {
  const [selectedReport, setSelectedReport] = useState<ReportType | null>(null);
  
  // Filters State
  const [loanFilter, setLoanFilter] = useState({
    employeeName: '',
    startDate: '',
    endDate: ''
  });

  const [payrollFilter, setPayrollFilter] = useState({
    month: new Date().getMonth() + 1,
    year: new Date().getFullYear()
  });

  // Helper to get employee code (Simulated lookup for demo consistency)
  const getEmpCode = (name: string) => MOCK_EMPLOYEES.find(e => e.name === name)?.employeeCode || '-';

  const reports: ReportType[] = [
    { id: 1, title: '  ', desc: '    ', icon: Clock, color: 'text-indigo-600', bg: 'bg-indigo-50', dataKey: 'attendance' },
    { id: 2, title: '  ', desc: '   ', icon: DollarSign, color: 'text-green-600', bg: 'bg-green-50', dataKey: 'payroll' },
    { id: 3, title: '  ', desc: '     ', icon: Users, color: 'text-blue-600', bg: 'bg-blue-50', dataKey: 'employees' },
    { id: 4, title: ' ', desc: '   ', icon: Briefcase, color: 'text-purple-600', bg: 'bg-purple-50', dataKey: 'recruitment' },
    { id: 5, title: ' ', desc: '    ', icon: PieChart, color: 'text-orange-600', bg: 'bg-orange-50', dataKey: 'leaves' },
    { id: 6, title: '  ', desc: '      ', icon: Wallet, color: 'text-red-600', bg: 'bg-red-50', dataKey: 'loans' },
  ];

  const handlePrint = () => {
    window.print();
  };

  const monthsList = [
    { id: 1, name: '' }, { id: 2, name: '' }, { id: 3, name: '' },
    { id: 4, name: '' }, { id: 5, name: '' }, { id: 6, name: '' },
    { id: 7, name: '' }, { id: 8, name: '' }, { id: 9, name: '' },
    { id: 10, name: '' }, { id: 11, name: '' }, { id: 12, name: '' },
  ];

  const renderReportContent = () => {
    if (!selectedReport) return null;

    switch (selectedReport.dataKey) {
      case 'attendance':
        const enrichedAttendance = MOCK_ATTENDANCE.map(r => ({...r, employeeCode: getEmpCode(r.employeeName)}));
        return (
          <div className="space-y-4">
             <div className="mb-4 flex justify-end no-print">
                <DataControls data={enrichedAttendance} fileName="attendance_report" isAdmin={true} />
             </div>
             <table className="w-full text-right text-sm border border-gray-200">
              <thead className="bg-gray-50 text-gray-700 font-bold">
                <tr>
                  <th className="p-3 border border-gray-200 w-20"></th>
                  <th className="p-3 border border-gray-200"></th>
                  <th className="p-3 border border-gray-200"></th>
                  <th className="p-3 border border-gray-200"></th>
                  <th className="p-3 border border-gray-200"></th>
                  <th className="p-3 border border-gray-200"></th>
                </tr>
              </thead>
              <tbody>
                {enrichedAttendance.map((row, idx) => (
                  <tr key={idx} className="border-b border-gray-100">
                    <td className="p-3 border-l border-gray-100 font-mono text-gray-500">{row.employeeCode}</td>
                    <td className="p-3 border-l border-gray-100">{row.employeeName}</td>
                    <td className="p-3 border-l border-gray-100">{row.date}</td>
                    <td className="p-3 border-l border-gray-100">{row.checkIn}</td>
                    <td className="p-3 border-l border-gray-100">{row.checkOut}</td>
                    <td className="p-3 border-l border-gray-100">{row.status}</td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        );
      case 'payroll':
        const filteredPayroll = MOCK_PAYROLL.filter(p => {
           // For demo, show all to ensure data visibility, normally check date
           return true; 
        }).map(p => ({...p, employeeCode: getEmpCode(p.employeeName)}));

        return (
          <div className="space-y-4">
             <div className="flex flex-wrap items-center gap-4 p-4 bg-gray-50 border border-gray-200 rounded-lg no-print">
                <div className="flex items-center gap-2">
                   <Calendar className="h-4 w-4 text-gray-500" />
                   <select 
                      value={payrollFilter.month}
                      onChange={(e) => setPayrollFilter({...payrollFilter, month: Number(e.target.value)})}
                      className="p-1.5 text-sm border border-gray-300 rounded focus:outline-none focus:ring-1 focus:ring-green-500"
                   >
                      {monthsList.map(m => <option key={m.id} value={m.id}>{m.name}</option>)}
                   </select>
                   <select 
                      value={payrollFilter.year}
                      onChange={(e) => setPayrollFilter({...payrollFilter, year: Number(e.target.value)})}
                      className="p-1.5 text-sm border border-gray-300 rounded focus:outline-none focus:ring-1 focus:ring-green-500"
                   >
                      {[2023, 2024, 2025].map(y => <option key={y} value={y}>{y}</option>)}
                   </select>
                </div>
                
                <div className="mr-auto flex gap-2">
                   <DataControls 
                      data={filteredPayroll} 
                      fileName={`payroll_report_${payrollFilter.year}_${payrollFilter.month}`} 
                      isAdmin={true}
                   />
                </div>
             </div>

             <div className="text-center mb-4">
                <h4 className="font-bold text-gray-800">   {monthsList.find(m => m.id === payrollFilter.month)?.name} {payrollFilter.year}</h4>
             </div>

             <table className="w-full text-right text-sm border border-gray-200">
              <thead className="bg-gray-50 text-gray-700 font-bold">
                <tr>
                  <th className="p-3 border border-gray-200 w-20"></th>
                  <th className="p-3 border border-gray-200"></th>
                  <th className="p-3 border border-gray-200"></th>
                  <th className="p-3 border border-gray-200"></th>
                  <th className="p-3 border border-gray-200"></th>
                  <th className="p-3 border border-gray-200"></th>
                </tr>
              </thead>
              <tbody>
                {filteredPayroll.length > 0 ? (
                   filteredPayroll.map((row, idx) => (
                    <tr key={idx} className="border-b border-gray-100">
                      <td className="p-3 border-l border-gray-100 font-mono text-gray-500">{row.employeeCode}</td>
                      <td className="p-3 border-l border-gray-100">{row.employeeName}</td>
                      <td className="p-3 border-l border-gray-100">{row.basicSalary.toLocaleString()}</td>
                      <td className="p-3 border-l border-gray-100">{row.allowances.toLocaleString()}</td>
                      <td className="p-3 border-l border-gray-100 text-red-600">{row.deductions.toLocaleString()}</td>
                      <td className="p-3 border-l border-gray-100 font-bold">{row.netSalary.toLocaleString()}</td>
                    </tr>
                  ))
                ) : (
                   <tr>
                      <td colSpan={6} className="p-6 text-center text-gray-500">    </td>
                   </tr>
                )}
              </tbody>
              {filteredPayroll.length > 0 && (
                 <tfoot className="bg-gray-50 font-bold">
                    <tr>
                       <td colSpan={2} className="p-3 border border-gray-200"></td>
                       <td className="p-3 border border-gray-200">{filteredPayroll.reduce((a, c) => a + c.basicSalary, 0).toLocaleString()}</td>
                       <td className="p-3 border border-gray-200">{filteredPayroll.reduce((a, c) => a + c.allowances, 0).toLocaleString()}</td>
                       <td className="p-3 border border-gray-200 text-red-600">{filteredPayroll.reduce((a, c) => a + c.deductions, 0).toLocaleString()}</td>
                       <td className="p-3 border border-gray-200">{filteredPayroll.reduce((a, c) => a + c.netSalary, 0).toLocaleString()}</td>
                    </tr>
                 </tfoot>
              )}
            </table>
          </div>
        );
      case 'employees':
        return (
          <div className="space-y-4">
             <div className="mb-4 flex justify-end no-print">
                <DataControls data={MOCK_EMPLOYEES} fileName="employees_report" isAdmin={true} />
             </div>
             <table className="w-full text-right text-sm border border-gray-200">
              <thead className="bg-gray-50 text-gray-700 font-bold">
                <tr>
                  <th className="p-3 border border-gray-200 w-20"></th>
                  <th className="p-3 border border-gray-200"></th>
                  <th className="p-3 border border-gray-200"></th>
                  <th className="p-3 border border-gray-200"></th>
                  <th className="p-3 border border-gray-200"> </th>
                  <th className="p-3 border border-gray-200"></th>
                </tr>
              </thead>
              <tbody>
                {MOCK_EMPLOYEES.map((row, idx) => (
                  <tr key={idx} className="border-b border-gray-100">
                    <td className="p-3 border-l border-gray-100 font-mono text-gray-500">{row.employeeCode}</td>
                    <td className="p-3 border-l border-gray-100">{row.name}</td>
                    <td className="p-3 border-l border-gray-100">{row.jobTitle}</td>
                    <td className="p-3 border-l border-gray-100">{row.department}</td>
                    <td className="p-3 border-l border-gray-100">{row.joinDate}</td>
                    <td className="p-3 border-l border-gray-100">{row.salary}</td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        );
      case 'recruitment':
        return (
          <div className="space-y-4">
             <div className="mb-4 flex justify-end no-print">
                <DataControls data={MOCK_CANDIDATES} fileName="recruitment_report" isAdmin={true} />
             </div>
             <table className="w-full text-right text-sm border border-gray-200">
              <thead className="bg-gray-50 text-gray-700 font-bold">
                <tr>
                  <th className="p-3 border border-gray-200"></th>
                  <th className="p-3 border border-gray-200"></th>
                  <th className="p-3 border border-gray-200"></th>
                  <th className="p-3 border border-gray-200"></th>
                </tr>
              </thead>
              <tbody>
                {MOCK_CANDIDATES.map((row, idx) => (
                  <tr key={idx} className="border-b border-gray-100">
                    <td className="p-3 border-l border-gray-100">{row.name}</td>
                    <td className="p-3 border-l border-gray-100">{row.position}</td>
                    <td className="p-3 border-l border-gray-100">{row.experience}</td>
                    <td className="p-3 border-l border-gray-100">{row.status}</td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        );
      case 'leaves':
        const enrichedLeaves = MOCK_LEAVE_BALANCES.map(b => ({...b, employeeCode: getEmpCode(b.employeeName)}));
        return (
          <div className="space-y-4">
             <div className="mb-4 flex justify-end no-print">
                <DataControls data={enrichedLeaves} fileName="leaves_balance_report" isAdmin={true} />
             </div>
             <table className="w-full text-right text-sm border border-gray-200">
              <thead className="bg-gray-50 text-gray-700 font-bold">
                <tr>
                  <th className="p-3 border border-gray-200 w-20"></th>
                  <th className="p-3 border border-gray-200"></th>
                  <th className="p-3 border border-gray-200"> </th>
                  <th className="p-3 border border-gray-200"></th>
                  <th className="p-3 border border-gray-200"> </th>
                  <th className="p-3 border border-gray-200"></th>
                </tr>
              </thead>
              <tbody>
                {enrichedLeaves.map((row, idx) => (
                  <tr key={idx} className="border-b border-gray-100">
                    <td className="p-3 border-l border-gray-100 font-mono text-gray-500">{row.employeeCode}</td>
                    <td className="p-3 border-l border-gray-100">{row.employeeName}</td>
                    <td className="p-3 border-l border-gray-100">{row.annualTotal}</td>
                    <td className="p-3 border-l border-gray-100">{row.annualUsed}</td>
                    <td className="p-3 border-l border-gray-100">{row.sickTotal}</td>
                    <td className="p-3 border-l border-gray-100">{row.sickUsed}</td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        );
      case 'loans':
        const filteredLoans = MOCK_LOANS.filter(loan => {
           const matchesEmployee = loan.employeeName.toLowerCase().includes(loanFilter.employeeName.toLowerCase());
           // Date filter logic is simplified for mock data display
           return matchesEmployee && loan.type === '';
        }).map(l => ({...l, employeeCode: getEmpCode(l.employeeName)}));

        return (
          <div className="space-y-4">
             <div className="flex flex-wrap gap-3 p-4 bg-gray-50 border border-gray-200 rounded-lg no-print">
                <div className="flex items-center gap-2">
                   <Users className="h-4 w-4 text-gray-500" />
                   <input 
                     type="text" 
                     placeholder=" " 
                     className="p-1.5 text-sm border border-gray-300 rounded focus:outline-none focus:ring-1 focus:ring-indigo-500"
                     value={loanFilter.employeeName}
                     onChange={(e) => setLoanFilter({...loanFilter, employeeName: e.target.value})}
                   />
                </div>
                <div className="flex items-center gap-2">
                   <Calendar className="h-4 w-4 text-gray-500" />
                   <input 
                     type="date" 
                     className="p-1.5 text-sm border border-gray-300 rounded focus:outline-none focus:ring-1 focus:ring-indigo-500"
                     value={loanFilter.startDate}
                     onChange={(e) => setLoanFilter({...loanFilter, startDate: e.target.value})}
                   />
                   <span className="text-gray-400">-</span>
                   <input 
                     type="date" 
                     className="p-1.5 text-sm border border-gray-300 rounded focus:outline-none focus:ring-1 focus:ring-indigo-500"
                     value={loanFilter.endDate}
                     onChange={(e) => setLoanFilter({...loanFilter, endDate: e.target.value})}
                   />
                </div>
                <div className="mr-auto">
                   <DataControls data={filteredLoans} fileName="active_loans_report" isAdmin={true} />
                </div>
             </div>

             <table className="w-full text-right text-sm border border-gray-200">
              <thead className="bg-gray-50 text-gray-700 font-bold">
                <tr>
                  <th className="p-3 border border-gray-200 w-20"></th>
                  <th className="p-3 border border-gray-200"></th>
                  <th className="p-3 border border-gray-200"> </th>
                  <th className="p-3 border border-gray-200"> </th>
                  <th className="p-3 border border-gray-200"> </th>
                  <th className="p-3 border border-gray-200"></th>
                </tr>
              </thead>
              <tbody>
                {filteredLoans.length > 0 ? (
                  filteredLoans.map((row, idx) => (
                    <tr key={idx} className="border-b border-gray-100">
                      <td className="p-3 border-l border-gray-100 font-mono text-gray-500">{row.employeeCode}</td>
                      <td className="p-3 border-l border-gray-100">{row.employeeName}</td>
                      <td className="p-3 border-l border-gray-100 font-mono">{row.date}</td>
                      <td className="p-3 border-l border-gray-100 font-bold">{row.amount.toLocaleString()} .</td>
                      <td className="p-3 border-l border-gray-100 text-red-600 font-medium">{row.remainingAmount.toLocaleString()} .</td>
                      <td className="p-3 border-l border-gray-100">
                         <span className={`px-2 py-1 rounded text-xs font-bold ${row.status === 'active' ? 'bg-green-100 text-green-700' : 'bg-gray-100 text-gray-600'}`}>
                            {row.status === 'active' ? '' : ''}
                         </span>
                      </td>
                    </tr>
                  ))
                ) : (
                   <tr>
                      <td colSpan={6} className="p-4 text-center text-gray-500">   </td>
                   </tr>
                )}
              </tbody>
              {filteredLoans.length > 0 && (
                 <tfoot className="bg-gray-50 font-bold">
                    <tr>
                       <td colSpan={3} className="p-3 border border-gray-200 text-left pl-4"></td>
                       <td className="p-3 border border-gray-200">{filteredLoans.reduce((a, c) => a + c.amount, 0).toLocaleString()} .</td>
                       <td className="p-3 border border-gray-200 text-red-600">{filteredLoans.reduce((a, c) => a + c.remainingAmount, 0).toLocaleString()} .</td>
                       <td className="p-3 border border-gray-200"></td>
                    </tr>
                 </tfoot>
              )}
            </table>
          </div>
        );
      default:
        return <p className="text-center py-10 text-gray-500">    </p>;
    }
  };

  return (
    <div className="space-y-6 animate-in fade-in duration-500">
      {/* Page Header (Hidden when printing) */}
      <div className="bg-white p-6 rounded-xl shadow-sm border border-gray-100 no-print">
        <h2 className="text-2xl font-bold text-gray-800"> </h2>
        <p className="text-sm text-gray-500 mt-1">      </p>
      </div>

      {/* Reports Grid (Hidden when printing) */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6 no-print">
        {reports.map((report) => (
          <div 
            key={report.id} 
            onClick={() => setSelectedReport(report)}
            className="group relative bg-white p-6 rounded-2xl border border-gray-100 shadow-sm hover:shadow-xl hover:-translate-y-1 transition-all duration-300 cursor-pointer overflow-hidden"
          >
            {/* Hover Background Effect */}
            <div className={`absolute top-0 right-0 w-32 h-32 rounded-full -mr-16 -mt-16 transition-opacity opacity-0 group-hover:opacity-10 ${report.bg.replace('bg-', 'bg-opacity-20 bg-')}`}></div>

            <div className="flex justify-between items-start mb-4 relative z-10">
               <div className={`w-14 h-14 ${report.bg} rounded-2xl flex items-center justify-center transition-transform group-hover:scale-110 shadow-sm`}>
                 <report.icon className={`h-7 w-7 ${report.color}`} />
               </div>
               <div className="opacity-0 group-hover:opacity-100 transition-opacity translate-x-2 group-hover:translate-x-0">
                 <div className="bg-gray-50 p-2 rounded-full border border-gray-100">
                    <ArrowLeft className="h-5 w-5 text-gray-600" />
                 </div>
               </div>
            </div>
            
            <h3 className="text-xl font-bold text-gray-800 mb-2 group-hover:text-indigo-700 transition-colors relative z-10">{report.title}</h3>
            <p className="text-sm text-gray-500 mb-6 min-h-[40px] leading-relaxed relative z-10">{report.desc}</p>
            
            <div className="flex items-center justify-between pt-4 border-t border-gray-50 relative z-10">
               <span className="text-xs font-bold text-gray-400 group-hover:text-indigo-500 transition-colors bg-gray-50 px-2 py-1 rounded">PDF, Excel</span>
               <div className="flex items-center gap-2 text-indigo-600 text-sm font-bold opacity-80 group-hover:opacity-100">
                  <span></span>
                  <Printer className="h-4 w-4" />
               </div>
            </div>
          </div>
        ))}
      </div>

      {/* Report Preview Modal (Acts as the printable page) */}
      {selectedReport && (
        <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/50 p-4 backdrop-blur-sm modal-overlay-print">
          <div className="bg-white w-full max-w-4xl h-[90vh] rounded-xl shadow-2xl flex flex-col overflow-hidden modal-content-print">
            
            {/* Modal Header */}
            <div className="p-4 border-b border-gray-200 flex justify-between items-center bg-gray-50 no-print">
              <div className="flex items-center gap-3">
                 <div className={`p-2 rounded-lg ${selectedReport.bg}`}>
                    <selectedReport.icon className={`h-5 w-5 ${selectedReport.color}`} />
                 </div>
                 <h3 className="text-lg font-bold text-gray-800">{selectedReport.title}</h3>
              </div>
              <div className="flex items-center gap-2">
                 <button 
                   onClick={handlePrint}
                   className="flex items-center gap-2 bg-indigo-600 text-white px-4 py-2 rounded-lg hover:bg-indigo-700 text-sm font-medium"
                 >
                    <Printer className="h-4 w-4" />
                    
                 </button>
                 <button 
                   onClick={() => setSelectedReport(null)}
                   className="p-2 text-gray-400 hover:text-gray-600 hover:bg-gray-100 rounded-full"
                 >
                   <X className="h-5 w-5" />
                 </button>
              </div>
            </div>

            {/* Report Content */}
            <div className="p-8 overflow-y-auto flex-1 bg-white print:p-0">
               {/* Print Header (Only visible in print) */}
               <div className="hidden print-only mb-8 text-center border-b border-gray-200 pb-4">
                  <h1 className="text-2xl font-bold text-gray-900 mb-1">{selectedReport.title}</h1>
                  <p className="text-sm text-gray-500"> : {new Date().toLocaleDateString('ar-EG')}</p>
               </div>

               {renderReportContent()}

               {/* Print Footer */}
               <div className="hidden print-only mt-8 pt-4 border-t border-gray-200 text-center text-xs text-gray-400">
                        Nizam HR -   
               </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default Reports;